table, th, td {
    border: 1px solid rgba(0, 0, 0, 0.61);
    border-collapse: collapse;
}

th {
    background-color: rgb(0, 110, 255);
    color: whitesmoke;
    padding: 8px;
}

th:hover {
    cursor: pointer;
}

td {
    padding: 3px;
}

h4 {
    margin-top: 50px;
    padding-left: 20px;
}